import "./Record.css";
import TitleCard from "../Card/TitleCard";
import Card from "../Card/Card";

const Record = () => {
  return (
    <TitleCard className="record">
      <div id="Record" className="title">
        <div className="title_name">Record</div>
        <div className="title_line"></div>
      </div>
      <Card className="record_discription">
        <div className="record_place"></div>
        <div className="record_period"> 2017.03 - 2023.02()</div>
        <div className="record_title">  - React (2022.09) </div>
        <div className="record_title">  -   (2022.07) </div>
        <div className="record_title">  - Python 2 (2022)</div>
        <div className="record_title">  - Python 1 (2021)</div>
        <div className="record_title">  - 3d   (2021)</div>
        <div className="record_title">  - C++   (2021) </div>
      </Card>
    </TitleCard>
  );
};

export default Record;
