import { ResponsivePie } from "@nivo/pie";

const Chart = () => {
  const handle = {
    padClick: (data) => {
      console.log(data);
    },

    legendClick: (data) => {
      console.log(data);
    },
  };

  return (
    // chart height 100%  chart    height 
    <div style={{ width: "600px", height: "250px", margin: "0 auto" }}>
      <ResponsivePie
        /**
         * chart  
         */
        data={[
          { id: "HTML", value: 1 },
          { id: "CSS", value: 1 },
          { id: "JS", value: 1 },
          { id: "REACT", value: 1 },
          { id: "", value: 1 },
          { id: "PYTHON", value: 1 },
        ]}
        /**
         * chart margin
         */
        margin={{ top: 40, right: 80, bottom: 80, left: 80 }}
        /**
         * chart   
         */
        innerRadius={0.3}
        /**
         * pad 
         */
        padAngle={1.8}
        /**
         * pad radius  (pad    )
         */
        cornerRadius={8}
        /**
         * chart 
         */
        colors={[
          "#61cdbb",
          "#f47560",
          "orange",
          "#9467bd",
          "#f1e15b",
          "#d62728",
        ]} //   
        // colors={{ scheme: 'nivo' }} // nivo     
        /**
         * pad border  
         */
        borderWidth={1}
        /**
         * link label skip  
         */
        arcLinkLabelsSkipAngle={0}
        /**
         * link label 
         */
        arcLinkLabelsTextColor="#000000"
        /**
         * link label   
         */
        arcLinkLabelsThickness={2}
        /**
         * link label   
         */
        arcLinkLabelsColor={{ from: "color" }} // pad  
        /**
         * label (pad  ) skip  
         */
        arcLabelsSkipAngle={10}
        theme={{
          /**
           * label style (pad  )
           */
          labels: {
            text: {
              fontSize: 14,
              fill: "#000000",
            },
          },
          /**
           * legend style (default    key )
           */
          legends: {
            text: {
              fontSize: 12,
              fill: "#000000",
            },
          },
        }}
        /**
         * pad  
         */
        onClick={handle.padClick}
        /**
         * legend  (default    key )
         */
        legends={[
          {
            anchor: "bottom", // 
            direction: "row", // item  
            justify: false, // ,   justify  
            translateX: 0, // chart X 
            translateY: 56, // chart Y 
            itemsSpacing: 0, // item 
            itemWidth: 100, // item width
            itemHeight: 18, // item height
            itemDirection: "left-to-right", // item   
            itemOpacity: 1, // item opacity
            symbolSize: 18, // symbol ( ) 
            symbolShape: "circle", // symbol ( ) 
            effects: [
              {
                //    (hover textColor olive )
                on: "hover",
                style: {
                  itemTextColor: "olive",
                },
              },
            ],
            onClick: handle.legendClick, // legend  
          },
        ]}
      />
    </div>
  );
};

export default Chart;
